<template>
	<view class="body">
		<view class="content">
			<view class="head">
				<view class="headLeft">
					<view v-if="data.data[current].question_type=='single_choice'">单选题（{{data.data[current].score || 0}}分）</view>
					<view v-else-if="data.data[current].question_type=='self'">主观题（{{data.data[current].score || 0}}分）</view>
					<view v-else>多选题（{{data.data[current].score || 0}}分）</view>
				</view>
				<view class="headRight">
					<view class="sheet" @click="toCard"></view>
					<view class="progress">{{current+1}}/{{data.data.length}}</view>
				</view>
			</view>
			<view class="title" v-html="data.data[current].title"></view>
		</view>
		<!-- 答题模块 -->
		<view :class="{answer:true, option:isOption==false,options:isOption==true}">
			<image src="../../static/assessment/card_icon.png" mode="widthFix" class="icon" @click="isOption=!isOption"></image>
			<view class="choice" v-if="data.data[current].question_type!='self'">
				<!-- 选过答案 -->
				<view v-if="seeResult">
					<!-- 检查答案 -->
					<view v-if="answer">
						<view class="cell" v-for="(item,index) in result.answer" :key="index">
							<view :class="{letter:true, true:item.answer=='true', false:(item.result==true && item.answer=='false')||(item.result==false && item.answer=='true')}">{{letter[index]}}</view>
							<view class="text" v-html="item.option"></view>
						</view>
					</view>
					<!-- 做过的答案 -->
					<view v-else>
						<view class="cell" v-for="(item,index) in data.data[current].question_log" :key="index">
							<view :class="{letter:true,true:item.answer=='true', false:(item.result==true && item.answer=='false')||(item.result==false && item.answer=='true')}">{{letter[index]}}</view>
							<view class="text" v-html="item.option"></view>
						</view>
					</view>
					<!-- 对错模块 -->
					<view class="result">
						<view class="true" v-if="isTrue==true">我答对了</view>
						<view class="false" v-else>我答错了</view>
					</view>
					<view v-if="current==data.data.length-1 && seeResult==true">
						<view class="next" @click="submit">提交</view>
					</view>
					<view class="choiceBottom" v-else>
						<view class="next" @click="last">上一题</view>
						<view class="next" @click="next">下一题</view>
					</view>
				</view>
				<!-- 没选过答案 -->
				<view v-else>
					<view class="cell" v-for="(item,index) in data.data[current].question" :key="index" @click="checkAswer(item,index)">
						<view :class="{letter:true,checked:item.check==true}">{{letter[index]}}</view>
						<view class="text" v-html="item.option"></view>
					</view>
					<view class="sureButton" @click="seeAnswer">确定</view>
				</view>
			</view>
			<view class="subjective" v-else>
				<view class="title">答案：</view>
				<view class="text" v-html="data.data[current].question"></view>
				<view class="bottomMoudel">
					<input type="number" placeholder-class="defult" placeholder="根据解析自主预估分数(整数)" v-model="num" :disabled="data.data[current].score_log!=null">
					<view v-if="current==data.data.length-1 && seeResult==true">
						<view class="next" @click="submit">提交</view>
					</view>
					<view class="buttons" v-else>
						<view class="next" @click="last" v-if="seeResult || data.data[current].score_log>0">上一题</view>
						<view class="next" @click="next" v-if="seeResult || data.data[current].score_log>0">下一题</view>
						<view class="next" @click="seeAnswer" v-else>确认</view>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import { question, checkAnswer, } from '@/common/http.api.js';
	export default{
		data() {
			return {
				current:0,
				isOption:false,
				token:'',
				letter:["A","B","C","D","E","F","G","H","I"], // 选项序号
				type:1, //1单选2多选
				num:'', //学生预估分数
				answer:false, //是否做题
				seeResult:false, //是否检查答案
				result:{}, //检查答案
				isTrue:true,
				subject_id:'',
				data:{}
			}
		},
		onLoad(option) {
			this.token = store.state.vuex_token
			let subject_id = uni.getStorageSync('subject_id');
			if(subject_id){
				this.subject_id = subject_id
			}
			console.log('subject_id:',subject_id)
		},
		onShow() {
			question({token:this.token, subject_id:this.subject_id}).then(res=>{
				console.log(res)
				let data = res.data.data
				data.data.map(item=>{
					if(item.question_type!='self'){
						for(let i = 0;i<=item.question.length-1;i++){
							item.question[i].check = false
						}
					}
				})
				this.data = data
				let current = uni.getStorageSync('current');
				let isAnswerCard = uni.getStorageSync('isAnswerCard');
				if(current && isAnswerCard==1){
					this.current = current
					console.log('this.current:',this.current)
				}else{
					this.current = res.data.data.log_index
				}
				console.log(this.data)
				if(this.data.data[this.current].score_log>0 || this.data.data[this.current].question_log!=null){
					this.seeResult = true
					this.result = this.data.data[this.current].question_log
					this.isTrue = true
					this.num = this.data.data[this.current].score_log
				}else{
					this.seeResult = false
					this.num = ''
				}
			}).catch(err=>{
				console.log(err)
			})
		},
		methods: {
			// 选择答案
			checkAswer(item,index){
				this.answer = true
				console.log(item)
				if(this.data.data[this.current].question_type=='single_choice'){
					//单选
					this.data.data[this.current].question.map(item=>item.check=false)
					this.data.data[this.current].question[index].check=true
				}else{
					//多选
					this.data.data[this.current].question[index].check=!this.data.data[this.current].question[index].check
				}
				console.log(this.data.data[this.current].question)
			},
			// 检查答案
			seeAnswer(){
				if(this.$u.utils.networkType()==true){ // 判断是否弱网
					if(this.data.data[this.current].question_type=='self'){ //是否主观题
						if(this.num && this.num<=this.data.data[this.current].score){ //是否打分
							this.seeResult = true //检查答案
							let params = {
								token: this.token,
								question_id: this.data.data[this.current].id,
								score:this.num, //自己估分
							}
							checkAnswer({...params}).then(res=>{
								console.log(res)
								this.result = res.data.data.answer
								res.data.data.score_log>0 ? this.isTrue = true : this.isTrue = false
								console.log('this.isTrue:',this.isTrue)
							}).catch(err=>{
								console.log(err)
							})
						} else {
							this.$u.toast("未打分或超过题目给分")
						}
					}else if(this.data.data[this.current].question_type=='single_choice'){
						if (this.answer==true) {//是否选择答案
							this.seeResult = true //检查答案
							let answers = []
							let keys = []
							for (let i in this.data.data[this.current].question) {
								keys.push(this.data.data[this.current].question[i])
							}
							keys.map((item) => {
								if (item.check == true) {
									return answers.push({
										answer: item.answer,
										option: item.option,
										result: true
									})
								} else {
									answers.push({
										answer: item.answer,
										option: item.option,
										result: null
									})
								}
							})
							let answer = JSON.stringify(answers)
							let params = {
								token: this.token,
								question_id: this.data.data[this.current].id,
								question: answer,
							}
							checkAnswer({...params}).then(res=>{
								console.log(res)
								if(res.data.code==-1){
									this.$u.toast(res.data.data)
								}else{
									this.result = res.data.data
									res.data.data.score_log>0 ? this.isTrue = true : this.isTrue = false
								}
							}).catch(err=>{
								console.log(err)
							})
						} else {
							this.$u.toast("先尝试一下吧~")
						}
					}else{
						let answers = []
						let keys = []
						let num = 0
						for (let i in this.data.data[this.current].question) {
							keys.push(this.data.data[this.current].question[i])
						}
						keys.map((item) => {
							if (item.check == true) {
								answers.push({
									answer: item.answer,
									option: item.option,
									result: true
								})
								num+=1;
							} else {
								answers.push({
									answer: item.answer,
									option: item.option,
									result: null
								})
							}
						})
						let answer = JSON.stringify(answers)
						if (this.answer==true && num>=2) {//是否选择答案 多选 
							this.seeResult = true //检查答案
							let params = {
								token: this.token,
								question_id: this.data.data[this.current].id,
								question: answer,
							}
							checkAnswer({...params}).then(res=>{
								console.log(res)
								if(res.data.code==-1){
									this.$u.toast(res.data.data)
								}else{
									this.result = res.data.data
									res.data.data.score_log>0 ? this.isTrue = true : this.isTrue = false
								}
							}).catch(err=>{
								console.log(err)
							})
						} else {
							this.$u.toast("至少选择两个答案~")
						}
					}
				}else{
					this.$u.toast('当前网络状况不佳~')
				}
			},
			// 提交估分结果
			submit() {
				uni.navigateTo({
					url:'/pages/assessment/answerCard?subject_id='+this.subject_id+'&last=1'
				})
			},
			// 去答题卡
			toCard() {
				uni.navigateTo({
					url:'/pages/assessment/answerCard?subject_id='+this.subject_id
				})
			},
			// 上一题
			last() {
				this.current-=1
				this.seeResult = true
				question({token:this.token, subject_id:this.subject_id}).then(res=>{
					console.log(res)
					let data = res.data.data
					data.data.map(item=>{
						if(item.question_type!='self'){
							item.question.map(a=>{
								a.check = false
							})
						}
					})
					this.data = data
					if(this.data.data[this.current].score_log>0){
						this.seeResult = true
						this.result = this.data.data[this.current].question_log
						this.isTrue = true
						this.num = this.data.data[this.current].score_log
					}
				}).catch(err=>{
					console.log(err)
				})
			},
			// 下一题
			next() {
				this.seeResult = false
				this.num = ''
				this.current+=1
				if(this.data.data[this.current].question_log!=null){
					this.seeResult = true
					this.data.data[this.current].score_log>0 ? this.isTrue = true : this.isTrue = false
				}
				if(this.data.data[this.current].score_log!=null){
					this.seeResult = true
					this.num = this.data.data[this.current].score_log
				}
			}
		}
	}
</script>

<style lang="scss">
	.body{
		width: 100vw;
		padding: 2vh 0;
		min-height: 96vh;
		background-color: #f7f6fb;
		.content{
			margin: 0 5%;
			border-radius: 20rpx;
			padding: 30rpx;
			background-color: #fff;
			.head{
				display: flex;
				justify-content: space-between;
				align-items: center;
				padding-bottom: 10rpx;
				border-bottom: 1rpx solid #eee;
				margin-bottom: 20rpx;
				.headLeft{
					font-size: 30rpx;
					view{
						display: inline-block;
					}
				}
				.headRight{
					display: flex;
					justify-content: space-between;
					align-items: center;
					font-size: 24rpx;
					.sheet{
						width: 90rpx;
						height: 70rpx;
						margin-right: 50rpx;
						background-image: url(../../static/assessment/answer_icon.png);
						background-repeat: no-repeat;
						background-position: center top;
						background-size: 38rpx;
						display: flex;
						justify-content: center;
						align-items: flex-end;
					}
					.sheet::after{
						content: '答题卡';
						font-size: 22rpx;
					}
					.progress{
						
					}
				}
			}
			.title{
				font-size: 30rpx;
				overflow: scroll;
			}
		}
		.answer{
			width: 100vw;
			left: 0;
			max-height: 60vh;
			overflow-y: scroll;
			position: fixed;
			.icon{
				width: 100vw;
				text-align: center;
				margin: 0 auto;
			}
			.choice{
				padding: 20rpx;
				margin-top: -16rpx;
				background-color: #fff;
				.choiceBottom{
					display: flex;
				}
				.cell{
					display: flex;
					align-items: flex-start;
					margin: 20rpx 0;
					margin-left: 50rpx;
					.letter{
						text-align: center;
						width: 74rpx;
						line-height: 74rpx;
						color: #cbcbcb;
						border-radius: 50%;
						border: 1rpx solid #eee;
						margin-right: 30rpx;
					}
					.checked{
						color: #3fc296;
						border: 1rpx solid #3fc296;
					}
					.true{
						background-color: #3fc296;
						border: 1rpx solid #3fc296;
						color: #fff;
					}
					.false{
						background-color: #ee2222;
						border: 1rpx solid #ee2222;
						color: #fff;
					}
					.text{
						font-size: 30rpx;
						line-height: 74rpx;
						width: 90%;
						overflow: scroll;
					}
				}
				.result{
					display: flex;
					justify-content: space-between;
					align-items: center;
					view{
						width: 100%;
						line-height: 88rpx;
						border-radius: 10rpx;
						text-align: center;
						font-size: 30rpx;
					}
					.true{
						background-color: #e5f9f0;
						color: #3fc295;
						background-image: url(../../static/assessment/true_icon.png);
						background-position: 70% center;
						background-repeat: no-repeat;
						background-size: 30rpx;
					}
					.false{
						background-color: #ffeded;
						color: #ff5454;
						background-image: url(../../static/assessment/false_icon.png);
						background-position: 70% center;
						background-repeat: no-repeat;
						background-size: 30rpx;
					}
				}
				.next{
					background-color: #3fc296;
					color: #fff;
					font-size: 28rpx;
					text-align: center;
					border-radius: 50rpx;
					line-height: 78rpx;
					width: 160rpx;
					margin: 20rpx auto;
				}
				.sureButton{
					width: 150rpx;
					text-align: center;
					background-color: #ffd656;
					color: #fff;
					font-size: 28rpx;
					line-height: 68rpx;
					border-radius: 50rpx;
					margin: 20rpx auto;
				}
			}
			.subjective{
				padding-top: 20rpx;
				margin-top: -16rpx;
				background-color: #fff;
				.title{
					font-size: 30rpx;
					margin: 0 20rpx;
				}
				.text{
					margin: 20rpx;
					overflow: scroll;
				}
				.bottomMoudel{
					display: flex;
					padding: 20rpx 30rpx;
					justify-content: space-between;
					align-items: center;
					background-color: #f5f5f5;
					.buttons{
						display: flex;
						view{
							margin: 0 20rpx;
						}
						view:last-child{
							margin: 0;
						}
					}
					.defult{
						line-height: 78rpx;
						color: #dbdbdb;
					}
					input{
						width: 100%;
						padding: 0 20rpx;
						height: 78rpx;
						line-height: 78rpx;
						font-size: 28rpx;
						border: 1rpx solid #eee;
						border-radius: 8rpx;
						background-color: #fff;
					}
					.next{
						background-color: #3fc296;
						color: #fff;
						font-size: 28rpx;
						text-align: center;
						border-radius: 50rpx;
						line-height: 78rpx;
						width: 160rpx;
					}
				}
			}
		}
		.option{
			top: 93vh;
			transition: ease-in-out 0.5s; 
		}
		.options{
			bottom: 0;
			transition: ease-in-out 0.5s; 
		}
	}
</style>